import { Injectable } from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http'
import { Character } from '@app/interface/character.interface';
import { environment } from '@env/environment.prod';

const headers = new HttpHeaders({
  'Content-Type':'application/json'
})

@Injectable({
  providedIn: 'root'
})
export class CharacterService {

  constructor(private http:HttpClient) { }

  getCharacters(query = '', page = 1){
    return this.http.get<Character[]>(`${environment.API}/?name=${query}&page=${page}`,{ headers })
  }

  getDetails(id:number){
    return this.http.get<Character>(`${environment.API}/${id}`)
  }
}
