import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { CharacterService } from '@app/shared/services/character.service';
import { map, switchMap } from 'rxjs/operators';

@Component({
  selector: 'app-characterdetails',
  templateUrl: './characterdetails.component.html',
  styleUrls: ['./characterdetails.component.css']
})
export class CharacterdetailsComponent implements OnInit {

  character:any

  constructor(private activerouter:ActivatedRoute, private characterService:CharacterService) { }

  ngOnInit(): void {
      
    this.activerouter.params
      .pipe( map( (param) => param.id ),
         switchMap( id => this.characterService.getDetails(id) ),
      ).subscribe( (res:any) =>  this.character = res)
    
  }

}
