import { AfterViewInit, Component, OnDestroy, OnInit } from '@angular/core';
import { Character } from '@app/interface/character.interface';
import { CharacterService } from '@app/shared/services/character.service';
import { map, pluck, switchMap, tap } from 'rxjs/operators'
import { Subscription } from 'rxjs'
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-characterlist',
  templateUrl: './characterlist.component.html',
  styleUrls: ['./characterlist.component.css']
})
export class CharacterlistComponent implements OnInit {

  characters:Character[]
  suscription:Subscription
 
  constructor(
    private characterService:CharacterService,
    private activeRouter:ActivatedRoute
    
    ) { }

  ngOnInit(): void {
   
    this.activeRouter.queryParams.pipe(
        map( (param) => param.query ),
        switchMap( (query) => this.characterService.getCharacters(query,1) ),
        pluck('results')
      ).subscribe( (res:any) => this.characters = res )
  }

  trackByItem(index, item){
    return item.id
  }

  details(id){
    console.log(id)
  }
}
